*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

.Home-BG{
  min-height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
}

.Home-BG {
  background: linear-gradient(180deg, #65dfc9, #2833ca, #6cdbeb);
  background-size: 600% 600%;

  -o-animation: AnimationName 59s ease infinite;
  animation: AnimationName 59s ease infinite;
}

@-o-keyframes AnimationName {
  0%{background-position:0% 2%}
  50%{background-position:100% 99%}
  100%{background-position:0% 2%}
}
@keyframes AnimationName {
  0%{background-position:0% 2%}
  50%{background-position:100% 99%}
  100%{background-position:0% 2%}
}

.glass{
  background: white;
  min-height: 75vh;
  width: 60%;
  background: linear-gradient(to right bottom, rgba(255,255,255,0.7),  rgba(255,255,255,0.3));
  border-radius: 2rem;
  z-index: 2;
  backdrop-filter: blur(2rem);
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
}

.circle1, .circle2{
  background: white;
  background: linear-gradient(to right bottom, rgba(255,255,255,0.8),  rgba(255,255,255,0.3));
  border-radius: 2rem;
  height: 15rem;
  width: 15rem;
  position: absolute;
  border-radius: 50%;
}

.circle1{
 top: 3%;
 right: 10%
}

.circle2{
  bottom: 5%;
  left: 8%;
}

.header{
  width: 100%;
  min-height: 10vh;
  border-radius: 2rem;
  display: flex;
  align-items: center;
  justify-content: space-between;
}



.logoDiv{
  text-align: center;
  margin-top: -2rem;
  margin-left: 5rem;
}

.menuBtn{
  font-size: 70px;
  color: #23538A;
  font-family: 'Poppins', sans-serif;
  font-size: medium;
  font-weight: 600;
  margin: 0;
  text-decoration: none;
  z-index: 2;
}

.NFTLink{
  font-size: 70px;
  color: #23538A;
  font-family: 'Poppins', sans-serif;
  font-size: medium;
  font-weight: 600;
  margin-top: 11px;
}

.NFTLink2{
  font-size: 70px;
  color: #23538A;
  font-family: 'Poppins', sans-serif;
  font-size: medium;
  font-weight: 600;
  margin-top: 14px;
}

.NFTLink:hover, .NFTLink2:hover{
  -webkit-text-fill-color: white;
  text-decoration: none;
}


.menuBtn:hover{
   -webkit-text-fill-color: white;
   text-decoration: none;
}

.logo{
  width: 15rem;
  height: 15rem;
  margin-top: -3rem;
  margin-bottom: -5.5rem;
}

h1{
  font-family: 'Ubuntu', sans-serif;
  background: #23538A;
background: -webkit-linear-gradient(to bottom right, #23538A 40%, #A7CFDF 75%, #65DFC9 70%);
background: -moz-linear-gradient(to bottom right, #23538A 40%, #A7CFDF 75%, #65DFC9 70%);
background: linear-gradient(to bottom right, #23538A 40%, #A7CFDF 75%, #65DFC9 70%);
-webkit-background-clip: text;
-webkit-text-fill-color: transparent;
  
}

.pageInfo{
  height: 40rem;
  width: 50rem;
  margin-top: 1.5rem;
  margin-left: 7.3rem;
  display: flex;
  flex-direction: column;
}

.infoBox1{
  height: 17rem;
  width: 65rem;
  font-size: large;
  margin-top: 1rem;
  display: flex;
  flex-direction: row;
}

h2{
  font-family: 'Poppins', sans-serif;
  color: #5583c0;
  font-weight: 900;
  font-size: 3.5rem;
  opacity: 0.8;
}

.infoBox2{
  height: 10rem;
  width: 40rem;
  margin-top: 3rem;
  
  margin-left: .2rem;
  font-size: large;
}

h3{
  font-family: 'Poppins', sans-serif;
  color: White;
  font-weight: 200;
  font-size: 1.6rem;
  opacity: 0.8;
}

.shopBtn{
  margin-top: 5.5rem;
  border-radius: 1rem;
  background: linear-gradient(to right bottom, rgba(60, 106, 233, 0.7),  rgba(255,255,255,0.3));
  border: transparent;
  color: white;
  width: 14rem;
  height: 3rem;
  font-weight: 300;
  font-family: 'Poppins', sans-serif;
  font-size: 2rem;
}

.shopBtn:hover{
  -webkit-transition: all 1s ease-out;
  -moz-transition: all 1s ease-out;
  -o-transition: all 1s ease-out;
  -ms-transition: all 1s ease-out;
  transition: all 1s ease-out;   
  border: 1px white solid;
}

.nftCard{

border-radius: 2rem;
display: flex;
justify-content: center;
background: linear-gradient(to right bottom, 
rgba(255,255,255,0.7),  rgba(255,255,255,0.3));
box-shadow: 0 14px 28px rgba(0,0,0,0.25), 0 10px 10px rgba(0,0,0,0.22);
}

.icon {
  position: relative;
  top: 150%;
  overflow: hidden;
  width: 22rem;
  height: 30rem;
  display: inline-block;
  box-shadow: 0 14px 28px rgba(0,0,0,0.25), 0 10px 10px rgba(0,0,0,0.22);
  margin: 25px 0 25px 25px;
  border-radius: 2rem;
  color: #fff;
  text-decoration: none;
  text-align: center;
  line-height: 50px;
  font-size: 12px;
  font-family: sans-serif;
}

.icon:nth-child(1) { background: hsla(229, 79%, 79%, 1);

  background: linear-gradient(315deg, hsla(229, 79%, 79%, 1) 27%, hsla(186, 98%, 83%, 1) 60%);
  
  background: -moz-linear-gradient(315deg, hsla(229, 79%, 79%, 1) 27%, hsla(186, 98%, 83%, 1) 60%); }


