}


.hover_bkgr_fricc{
  background:rgba(0,0,0,.4);
  cursor:pointer;
  display:none;
  height:100%;
  position:fixed;
  text-align:center;
  top:0;
  width:100%;
  z-index:10000;
}
.hover_bkgr_fricc .helper{
  display:inline-block;
  height:100%;
  vertical-align:middle;
}
.hover_bkgr_fricc > div {
  background: linear-gradient(to right bottom, rgba(255,255,255,0.7),  rgba(255,255,255,0.3));
  box-shadow: 10px 10px 60px #555;
  display: inline-block;
  height: auto;
  max-width: 551px;
  min-height: 100px;
  vertical-align: middle;
  width: 60%;
  position: relative;
  border-radius: 8px;
  padding: 15px 5%;
  border: black 2px solid
}
.popupCloseButton {
  background-color: #fff;
  border: 3px solid #999;
  border-radius: 50px;
  cursor: pointer;
  display: inline-block;
  font-family: arial;
  font-weight: bold;
  position: absolute;
  top: -20px;
  right: -20px;
  font-size: 25px;
  line-height: 30px;
  width: 30px;
  height: 30px;
  text-align: center;
}
.popupCloseButton:hover {
  background-color: #ccc;
}
.trigger_popup_fricc {
  cursor: pointer;
  font-size: 20px;
  display: inline-block;
  font-weight: bold;
  margin-left: 20rem;
  background-color: #ED184B;
  color: white;
  width: 8rem;
  height: 1.5rem;
  text-align: center;
  border-radius: 3px;
  border: black 2px solid;
}

.trigger_popup_fricc:hover{
  border: white 2px solid;
}

.InfoBtn{
  background: none;
  width: 1rem;
  border:none;
  height: 1rem;
  border-radius: 2rem;
  font-weight: 300;
  font-family: 'Poppins', sans-serif;
  font-size: 10px;
}

.InfoImg{
  width: 1.4rem;
  height: 1.4rem;
}

.text{
  color: black;
  margin-top: 1rem;
}